.navbar-brand {
  margin-right: 0;
  text-align: left;

  img {
    height: auto;
    margin: 0 auto;
    max-width: 200px;
    width: 100%;
  }

  @media screen and (min-width: map-get($breakpoints, medium)) {

    img {
      margin: 0;
      max-width: 100%;
      width: 500px;
    }
  }
}
